  list-style-type: none;
  display: flex;
}

.home-btn {
  color: var(--text-color-light);
  background-color: var(--light-theme-color);
  font-weight: var(--default-weight);
  font-size: var(--button-size);
  border: 1px solid #fff;
  border-radius: 100px;
  margin-left: -5px;
  padding: 14px 24px;
}

.library-btn {
  color: var(--text-color-light);
  background-color: var(--light-theme-color);
  font-weight: var(--default-weight);
  font-size: var(--button-size);
  border: 1px solid #fff;
  border-radius: 100px;
  padding: 14px 24px;
}

.logo-title {
