  margin-right: 16px;
}

.title {
  color: var(--light-theme-color);
  font-weight: var(--default-weight);
  font-size: 64px;
  line-height: 75px;
  position: absolute;
  bottom: 12px;
}

.nav-list {
  display: flex;
}

.search-form {
  background-color: var(--light-theme-color);
  border: 1px solid #fff;
  border-radius: 100px;
  outline: none;
  margin-top: 10px;
  margin-left: 32px;
  margin-right: -20px;
  padding: 10px;
  display: flex;
}

.search-form input {
