}

.page-button {
  color: #000;
  background-color: #f7f7f7;
  border: 1px solid #b92f2c;
  border-radius: 8px;
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
}

.page-button:hover, .page-button.active {
  color: #fff;
  background-color: #b92f2c;
}

.arrow {
  cursor: pointer;
