  }

  .arrow {
    padding: 8px 16px;
    font-size: 18px;
  }
}

@media (max-width: 320px) {
  .content {
    max-width: 100%;
    padding: 10px;
  }

  .page-button {
    margin: 2px;
    padding: 6px 12px;
    font-size: 12px;
  }

  .arrow {
    padding: 6px 12px;
    font-size: 16px;
  }
}

